.modal-popup-custom {
  max-width: 880px; }
  .modal-popup-custom .modal-content {
    padding-left: 35px;
    padding-right: 35px;
    padding-bottom: 30px;
    border-radius: 20px; }
  .modal-popup-custom .modal-header {
    border-bottom: none; }
    .modal-popup-custom .modal-header h3 {
      font-weight: bold;
      font-size: 24px;
      color: #2C3652; }
    .modal-popup-custom .modal-header .close {
      transform: translateX(20px);
      font-size: 32px; }
  .modal-popup-custom .modal-footer {
    border-top: none; }
