.multi-drop-v3 .selected-container .icon-container {
  display: flex;
  align-items: center; }
  .multi-drop-v3 .selected-container .icon-container img {
    margin: 0 8px 0 15px;
    filter: brightness(0) saturate(100%) invert(85%) sepia(19%) saturate(155%) hue-rotate(182deg) brightness(86%) contrast(91%);
    width: 15px;
    height: 15px; }

@keyframes animate-open {
  0% {
    z-index: 1000; }
  100% {
    z-index: 1000; } }

@keyframes animate-close {
  0% {
    z-index: 1000; }
  100% {
    z-index: 0; } }

.drop-open {
  animation: animate-open 3s infinite; }

.drop-close {
  animation: animate-close 3s linear; }

@keyframes animate-z-visible-v3 {
  0% {
    z-index: -999; }
  1% {
    z-index: 1029; }
  100% {
    z-index: 1029; } }

@keyframes animate-z-invisible-v3 {
  0% {
    z-index: 1029; }
  99% {
    z-index: 1029; }
  100% {
    z-index: -999; } }

@keyframes animate-visible {
  0% {
    transform: scale(0);
    transform-origin: top left; }
  100% {
    transform: scale(1);
    transform-origin: top left; } }

@keyframes animate-invisible {
  0% {
    transform: scale(1);
    transform-origin: top left; }
  100% {
    transform: scale(0);
    transform-origin: top left; } }

.no-internal-desc input {
  max-height: 32px !important;
  transform: translateY(7px) !important; }

.multi-drop-v3 .dropdown-container .title-header {
  padding-left: 45px; }

.multi-drop-v3 .selected-container {
  display: flex; }
  .multi-drop-v3 .selected-container .selected-text {
    padding-left: 44px; }
  .multi-drop-v3 .selected-container .selected-items-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-right: 45px; }
    .multi-drop-v3 .selected-container .selected-items-container .selected-text-multi {
      height: 36px;
      border-radius: 12px;
      display: inline-block;
      position: relative;
      padding: 0;
      overflow: inherit;
      flex-grow: 1;
      padding-top: 5px;
      width: auto; }
      .multi-drop-v3 .selected-container .selected-items-container .selected-text-multi input {
        height: auto;
        transform: translateY(5px);
        text-overflow: ellipsis; }
        .multi-drop-v3 .selected-container .selected-items-container .selected-text-multi input::placeholder {
          font-weight: normal;
          color: #B6BDCB;
          text-overflow: ellipsis; }
    .multi-drop-v3 .selected-container .selected-items-container .selected-item-container {
      height: 24px;
      border-radius: 12px;
      padding: 0 8px;
      margin: 4px;
      border: 1px solid #CADBEA;
      display: inline-block; }
      .multi-drop-v3 .selected-container .selected-items-container .selected-item-container .selected-item {
        display: flex;
        align-items: center;
        height: 100%; }
        .multi-drop-v3 .selected-container .selected-items-container .selected-item-container .selected-item p {
          margin: 0;
          font-weight: bold;
          font-size: 12px;
          color: #2C3652; }
        .multi-drop-v3 .selected-container .selected-items-container .selected-item-container .selected-item .selected-item-destroy {
          display: flex;
          align-items: center; }
          .multi-drop-v3 .selected-container .selected-items-container .selected-item-container .selected-item .selected-item-destroy img {
            margin-left: 8px;
            width: 8px;
            height: 8px;
            filter: brightness(0) saturate(100%) invert(68%) sepia(3%) saturate(1666%) hue-rotate(182deg) brightness(91%) contrast(85%); }
        .multi-drop-v3 .selected-container .selected-items-container .selected-item-container .selected-item .selected-item-destroy:hover {
          filter: brightness(0) saturate(100%) invert(44%) sepia(11%) saturate(1407%) hue-rotate(140deg) brightness(89%) contrast(83%); }
